<template>
  <div class="writeArticle">
  	<div class="writeArticleTitle">
  	  <span class="back" @click="goBack">文章管理</span>
   	  <i class="el-icon-arrow-right"></i> 
      <span>文章编辑</span>
  	</div> 
    <div class="editorC" >
      <div class="editorButton">
        <input type="text" placeholder="在这里输入标题" v-model.trim="EditorTitle">
      </div>
    </div>
    <div class="content">
      <div class="editorContent">
        <div id="editor" class="editor"></div>
        <div class="editorVideo">
          <el-upload class="upload-demo" :action="singleUploadFile" :on-change="handleChange"
            :file-list="fileList" name="fileList" :before-upload='selectFileType' :on-success='getFileUrl' :show-file-list='uploadProgress'>
            <i class="el-icon-video-camera"></i>
          </el-upload>
        </div>
        <!-- <div class="editorPlace" v-if="isShowInput">
          <input type="text" placeholder="在这里输入内容">
        </div> -->
    	</div>
    </div>
    <div class="submitBtn">
      <span class="save" @click="saveDraft">保存并退出</span>
      <span class="report" @click="publishArticle">发布文章</span>
    </div>
  </div>
</template>
<style lang="less" scoped>
	.writeArticle {
    margin: 60px 0 60px 260px;
    .writeArticleTitle{
      color: #808080;
  	  height: 58px;
  	  font-size: 14px;
  	  line-height: 58px;
  	  text-align: left;
  	  padding-left: 50px;
  	  border-bottom: 1px solid #eee;
  	  .back{
  	  	color: black;
  	  	cursor: pointer;
  	  }
    }
     .editorC{
          text-align: left;
          z-index: 10000;
          padding: 0 50px;
          .editorButton{
            height: 80px;
            border-bottom: 1px solid #e5e5e5;
            input{
              width:100%;
              height: 100%;
              border:none;
              padding-left: 20px;
              font-size: 20px;
              color: #616161;
            }
            input::placeholder{
              font-size: 20px;
              color: #ccc;
            }
          }
        }  
    .content{
      padding:0 50px;
      .editorContent{
        margin-top: 20px;
        position: relative;
        overflow: hidden;
        text-align: left;
        .editorVideo{
          font-size: 18px;
          position: absolute;
          top: 5px;
          left: 655px;
          color: #999;
          z-index: 100000;
          cursor: pointer;
        } 
        .editorVideo:hover{
          color: #333333;
        } 
        .editorPlace{
          position: absolute;
          top: 45px;
          left: 20px;
          input{
            border: none;
            font-size: 20px;
          }
          input::placeholder{
            color: #ccc;
          }
        }
      }
    }
    .submitBtn{
      margin-top: 50px;
      span{
        display: inline-block;
        width: 140px;
        height: 34px;
        line-height: 22px;
        border-radius: 4px;
        text-align: center;
        line-height: 34px;
        cursor: pointer;
      }
      .report:hover{
        opacity: 0.8;
      }
      .save{
        background: rgb(232,232,232);
        color: rgb(76,180,231);
      }
      .report{
        background: rgb(76,180,231);
        color: white;
        margin-left: 20px; 
      }
    }
    .article{
      padding: 20px 50px;
      img{
        width: 100%;
      }
    }
  }
</style>
<script>
import E from 'wangeditor';
import {mapState} from 'vuex';
export default {
  data () {
    return {
     EditorTitle:'',
     EditorText:'',
     articleData : this.$route.query.articleData,
     fileList: [],
     singleUploadFile:this.api.configIp+this.net.singleUploadFile,//文件上传地址
     editorDom:'',
     uploadProgress:true,
     isShowInput:true,
    }
  },
  computed: {
    ...mapState({
      userInfo:(state)=>state.mutations.userInfo
    })
  },
  mounted() {
    this.initEditor();
    //this.FromEditInit()
  },
  methods:{
  	goBack() {
  	  this.$router.push({
     		path:"/informationManage"
      })
  	},
  	initEditor(){
      var editor = new E('#editor');
      console.log(editor)
      editor.customConfig.onchange = (html) => {
        console.log('html:',html)
        console.log('222:',editor.txt.text())
        this.EditorText = html;
        // if(editor.txt.text()){
        //   this.isShowInput = false
        // }else{
        //   this.isShowInput = true
        // }
      }
      editor.customConfig.pasteFilterStyle = false;
      editor.customConfig.uploadImgServer =this.api.configIp + this.net.singleUploadFile//上传图片的接口
      editor.customConfig.uploadFileName = 'fileList';//自定义的文件名
      editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024; // 将图片大小限制为 3M
      editor.customConfig.uploadImgHeaders = {
          'Accept': '*/*',
          'Authorization':'Bearer ',    //头部token
          'requestType': 'zuul'
      }
      editor.customConfig.menus = [          //菜单配置
          'head',  // 标题
          'bold',  // 粗体
          'fontSize',  // 字号
          'fontName',  // 字体
          'italic',  // 斜体
          'underline',  // 下划线
          'strikeThrough',  // 删除线
          'foreColor',  // 文字颜色
          'backColor',  // 背景颜色
          'link',  // 插入链接
          'list',  // 列表
          'justify',  // 对齐方式
          'quote',  // 引用
          'emoticon',  // 表情
          'image',  // 插入图片
          'table',  // 表格
          'code',  // 插入代码
          'undo',  // 撤销
          'redo'  // 重复
      ] 
      editor.customConfig.uploadImgHooks = {
        customInsert:  (insertImg, result, editor)=> {
          console.log(result)
          var url = result.data.fullPath;   
          insertImg(url);
        }
      }
      editor.create();
      this.editorDom = editor
      if(this.articleData){
         editor.txt.html(this.articleData.content)
         this.EditorTitle = this.articleData.title;
         this.EditorText = this.articleData.content
      }
    },
    publishArticle(){
      let url = this.api.configIp + this.net.newsRelease;
      let userId = this.userInfo.id;
      console.log('内容:',this.EditorText)
      console.log('内容2:',this.editorDom.txt.text())
      if(this.EditorTitle==''){
        this.$message.warning('请输入标题!');
        return
      }else if(this.editorDom.txt.text() == ''){
        this.$message.warning('请输入内容!');
        return
      }
      let params;
      if(this.articleData){
        params={
          "id":this.articleData.id,
          "title":this.EditorTitle,
          "content":this.EditorText,
          "publishUser":userId
        }
      }else{
         params={
          "title":this.EditorTitle,
          "content":this.EditorText,
          "publishUser":userId
        }
      }
      console.log('params:',params)
      this.$post(url,params).then((res)=>{
        console.log('sucess:',res)
        if(res.code == 200 ){
          this.$message.success('发布成功!');
          this.clearText()
          setTimeout(()=>{
            this.$router.push('/informationManage')
          },1500)
        }else{
          this.$message.warning('发布失败!')
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
    saveDraft(){
      let url = this.api.configIp + this.net.newsDraftSave;
      let userId = this.userInfo.id;
      if(this.EditorTitle==''){
        this.$message.warning('请输入标题!');
        return
      }else if(this.editorDom.txt.text() == ''){
        this.$message.warning('请输入内容!');
        return
      }
      let params;
      if(this.articleData){
        params={
          "id":this.articleData.id,
          "title":this.EditorTitle,
          "content":this.EditorText,
          "publishUser":userId
        }
      }else{
         params={
          "title":this.EditorTitle,
          "content":this.EditorText,
          "publishUser":userId
        }
      }
      this.$post(url,params).then((res)=>{
        console.log('sucess:',res)
        if(res.code == 200 ){
          this.$message.success('保存成功!');
          this.clearText()
          setTimeout(()=>{
            this.$router.push('/informationManage')
          },1500)
        }else{
          this.$message.warning('保存失败!')
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
    clearText(){
      this.EditorTitle = '';
      this.EditorText = ''
    },
    handleChange(file, fileList) {
      this.fileList = fileList.slice(-3);
    },
    //上传成功前
    selectFileType(file){
      console.log("文件:",file);
      this.uploadProgress = true;
      let fileType = file.type;
      if(fileType.indexOf('video')==-1){
         this.$message.error('请上传视频格式文件!');
         return false;
      }
    },
    //上传成功
    getFileUrl(res, file, fileList){
      if(res.code==200){
        const videoUrl = res.data.fullPath;
        const text = '<video width="640" height="360" controls src="'+videoUrl+'"></video>'
        this.editorDom.cmd.do('insertHTML',text)
        //this.editorDom.txt.append(text)
        this.uploadProgress = false
      }
    },
    deleteVideo(event){
      const parentNode = event.target.parentNode;
      const siblingNode = event.target.previousElementSibling || event.target.previousSibling; 
      parentNode.removeChild(siblingNode)
    }
  }
}
</script>


